.instructor-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0px 50px;
}
.instructor-design {
  width: 50%;
  background: #407bff;
}
.instructor-signup {
  width: 50%;
}
.register {
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0px 50px;
}
.registerTab {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.registerTab button {
  padding: 10px 56px 10px 0px;
  margin-right: 50px;
  border: none;
  background: none;
  cursor: pointer;
  font-weight: 400;
  font-size: 30px;
  line-height: 20px;
  color: rgba(224, 224, 224, 0.5);
}

.registerTab button.active {
  font-weight: 700;
  color: #407bff;
  border-bottom: 2px solid #407bff;
}
.tabForm {
  width: 480px;
  padding: 20px 0px;
}
form {
  display: flex;
  flex-direction: column;
}
