select,
::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1em;
  /* color: #E0E0E0; */
}
input[type="checkbox"] {
  height: 20px;
  width: 20px;
  background-color: #eee;
}
.check {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
}
.check label {
  font-size: 14px;
  padding-left: 10px;
}
.formButton,
.loginButton {
  width: 100%;
  display: flex;
  justify-content: center;
}
.loginButton {
  justify-content: space-evenly;
  align-items: center;
}
input[type="submit"] {
  width: 100%;
  height: 60px;
  color: #fff;
  font-weight: 500;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  background: #407bff;
}
.loginButton input[type="submit"] {
  width: 180px;
}
.loginButton a {
  font-weight: 400;
  font-size: 18px;
  color: #407bff;
  text-decoration: none;
}
.password{
  position: relative;
  margin-top: 0px;
}
.password input{
  width: 100%;
}
.showPassword{
  position: absolute;
  top: 10px;
  right: 15px;
}

@media (max-width: 900px) {
  .register {
    padding: 50px 0px;
    margin: 0px;
  }
  .tabForm {
    width: 95%;
  }
  .registerTab {
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .registerTab button {
    padding: 10px 56px;
    margin-right: 0px;
    border: none;
    background: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 30px;
    line-height: 20px;
    color: rgba(224, 224, 224, 0.5);
  }
}

@media (max-width: 480px) {
  .registerTab {
    display: flex;
    justify-content: space-between;
  }
  .tabForm {
    width: 95%;
  }
  .registerTab button {
    padding: 10px 30px;
    font-size: 20px;
  }
  input[type="text"],
  input[type="email"],
  input[type="password"],
  select {
    padding: 15px 0px;
    padding-left: 20px;
  }
  ::placeholder,
  select {
    font-size: 14px;
  }
  input[type="submit"] {
    width: 120px;
    height: 40px;
    font-size: 18px;
  }
  .loginButton input[type="submit"] {
    width: 150px;
  }
}
